import React, { useState, useEffect } from 'react';
import Form from './Components/Form';
import Card from './Components/Card';
import "../node_modules/tailwindcss/tailwind.css";
import "./output.css";
import { api, api_key } from "./api/api";
import { Map } from './Components/Map';
import Swal from 'sweetalert2';
import Snowfall from './Components/Snowfall';

const App = () => {
  const [data, setData] = useState({});
  const [position, setPosition] = useState([16.77621, 96.1424053]);
  const [dataSource, setDataSource] = useState("city");
  const [locationName, setLocationName] = useState("");
  const [loading, setLoading] = useState(false);
  const [isFirstLoad, setIsFirstLoad] = useState(true);
  const [isOnline, setIsOnline] = useState(navigator.onLine);

  const fetchDataCity = async (cityName) => {
    try {
      const res = await api.get(`weather?q=${cityName}&appid=${api_key}`);
      setData(res.data);
      setPosition([res.data.coord.lat, res.data.coord.lon]);
      setDataSource("city");
      setIsFirstLoad(false);
    } catch {
      Swal.fire({
        title: "!",
        text: "     ",
        icon: "warning",
        confirmButtonText: "",
      });
    } finally {
      setLoading(false);
    }
  };

  const fetchDataPosition = async (lat, lon) => {
    try {
      const res = await api.get(`weather?lat=${lat}&lon=${lon}&appid=${api_key}`);
      setData(res.data);
      setPosition([res.data.coord.lat, res.data.coord.lon]);
      setDataSource("map");
      setIsFirstLoad(false);
    } catch {
      Swal.fire({
        title: "!",
        text: " ",
        icon: "warning",
        confirmButtonText: "",
      });
    }
  };

  const handleMapClick = async (lat, lon) => {
    setLoading(true);
    try {
      await fetchDataPosition(lat, lon);
  
      const response = await fetch(`https://nominatim.openstreetmap.org/reverse?format=json&lat=${lat}&lon=${lon}`);
      const data = await response.json();
      setLocationName(data.display_name || "Unknown Location");
    } catch {
      Swal.fire({
        title: "",
        text: "  ",
        icon: "error",
        confirmButtonText: "",
      });
    } finally {
      setLoading(false);
    }
  };

  const fetchUserLocation = () => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          const { latitude, longitude } = position.coords;
          fetchDataPosition(latitude, longitude);
        },
        () => {
          Swal.fire({
            title: "",
            text: "  location ",
            icon: "warning",
            confirmButtonText: "",
          });
          fetchDataCity("yangon");
        }
      );
    } else {
      Swal.fire({
        title: "",
        text: " browser  Geolocation API ",
        icon: "error",
        confirmButtonText: "",
      });
      fetchDataCity("yangon");
    }
  };

  useEffect(() => {
    fetchUserLocation();

    const handleOnline = () => {
      setIsOnline(true);
      Swal.close();
    };

    const handleOffline = () => {
      setIsOnline(false);
      Swal.fire({
        title: "    ",
        text: "  ",
        icon: "warning",
        confirmButtonText: "",
      });
    };

    if (!navigator.onLine) {
      handleOffline();
    }

    window.addEventListener('online', handleOnline);
    window.addEventListener('offline', handleOffline);

    return () => {
      window.removeEventListener('online', handleOnline);
      window.removeEventListener('offline', handleOffline);
    };
  }, []);

  return (
    <div className="wrapper">
      <Snowfall />
      <h1 className="title">   </h1>
      <Form fetchDataCity={fetchDataCity} />
      <div className="innerWrapper">
        <Map position={position} onMapClick={handleMapClick} onLocationChange={setLocationName}/>
        <Card data={data} dataSource={dataSource} locationName={locationName} loading={loading} isFirstLoad={isFirstLoad}/>
      </div>
    </div>
  );
};

export default App;
