import React from 'react'

const Snowfall = () => {
    return (
      <div className="absolute top-0 md:top-[30px] left-0 w-full h-full overflow-hidden pointer-events-none z-[-1]">
        {[...Array(50)].map((_, i) => (
          <div
            key={i}
            className="w-2 h-2 bg-white rounded-full opacity-70 absolute animate-snowfall"
            style={{
              left: `${Math.random() * 100}%`,
              animationDelay: `${Math.random() * 1}s`,
              animationDuration: `${Math.random() * 5 + 5}s`,
            }}
          ></div>
        ))}
      </div>
    );
  };

export default Snowfall
