import React, {useState, useEffect} from "react";
import Clock from 'react-clock';
import 'react-clock/dist/Clock.css';

const Card = ({ data, locationName, dataSource, loading, isFirstLoad }) => {
  const [value, setValue] = useState(new Date());
  const iconUrl = `https://openweathermap.org/img/wn/${data.weather?.[0]?.icon}@2x.png`;

  const kelvinToCelsius = (kelvin) => {
    return (kelvin - 273.15).toFixed(2);
  };

  const getTitle = () => {
    if (dataSource === "map" && locationName) {
      return locationName;
    }
    return `${data.name}, ${data.sys?.country}`;
  };

  useEffect(() => {
    const interval = setInterval(() => setValue(new Date()), 1000);

    return () => {
      clearInterval(interval);
    };
  }, []);

  if (JSON.stringify(data) !== "{}") {
    return (
      <div>
        <div className="h-[100%] pb-[20px] md:pb-0">
          <div className="cardWrapper">
          <Clock value={value} className="clock"/>
            <p className="cardTime">
              {new Date().toLocaleString("en-US", {
                month: "long",
                day: "numeric",
                year: "numeric"
              })}
            </p>
            <p className="cardTmp">
              <span className="cardDeg">
                {" "}
                {kelvinToCelsius(data.main?.temp)}C
              </span>
            </p>
            <div className="cardStateWrapper">
              <img src={iconUrl} alt="" />
              <p className="cardStateDay">{data.weather?.[0]?.description}</p>
            </div>
            {!isFirstLoad && <p className="cardTitle">{getTitle()}</p>}
          </div>
        </div>
      </div>
    );
  } else {
    return <p className="loader"><span>loading</span></p>
  }
};

export default Card;
