import React, {useRef} from 'react'

const Form = ({fetchDataCity}) => {

  const cityName = useRef();
  const searchWeather = () => {
    fetchDataCity(cityName.current?.value)
    cityName.current.value = ''
  }

  return (
    <div>
        <div className="mt-3">
          <input type="text" className='inputGroup' ref={cityName}
          
          onKeyDown={(e) => {
            if(e.key === "Enter") {
              searchWeather()
            }
          }}

          placeholder=' ...'/>
          <button className='btnSearch' onClick={() => searchWeather()}>Search</button>
        </div>
    </div>
  )
}

export default Form
